.board-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-panel {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  font-size: 20px;
}

.info-panel > div {
  margin: 0 10px;
}

.info-panel > div:not(:last-child)::after {
  content: '|';
  margin-left: 10px;
}

.board {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  padding: 1rem;
  box-sizing: border-box;
}

.board > * {
  aspect-ratio: 1 / 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: clamp(1rem, 3vw, 2rem);
}

@media (max-width: 768px) {
  .board {
    gap: 0.5rem;
    padding: 0.5rem;
  }
}